@import 'partials/variables';

#mci-options
{
	*
	{
		user-select: none;
	}

	.form-table
	{
		width:           auto;
		border-collapse: separate;
		border-spacing:  0 2 * $gs;

		tr
		{
			td
			{
				border-right: 1px solid #E5E5E5;
			}

			th
			{
				padding-left: $gs;
				border-left:  1px solid #E5E5E5;
			}

			td, th
			{
				background:    white;
				box-shadow:    0 1px 1px rgba(0, 0, 0, .04);
				border-top:    1px solid #E5E5E5;
				border-bottom: 1px solid #E5E5E5;
			}
		}
	}

	.add, .remove
	{
		user-select: none;
	}

	.id, .post_type, .dummy
	{
		display: none;
	}

	.size
	{
		margin-bottom: $gs;
		background:    #F7F7F7;
		padding:       $gs / 2 44px $gs / 2 $gs;
		border:        1px solid #C7C7C7;
		position:      relative;

		input
		{
			height:       30px;
			margin-right: $gs;
		}

		&::after
		{
			content:     "\A";
			white-space: pre;
		}
	}

	.mci-control
	{
		position:          absolute;
		border-left:       1px solid #C7C7C7;
		right:             0;
		width:             44px;
		height:            44px;
		cursor:            pointer;
		background-repeat: no-repeat;

		&.disabled
		{
			background: none;
			cursor:     default;

			&:hover
			{
				background-color: #F7F7F7;
			}
		}

		&:hover
		{
			background-color: #D7D7D7;
		}
	}

	.mci-remove
	{
		top:                 0;
		border-bottom:       1px solid #C7C7C7;
		background-image:    url('../img/delete.svg');
		background-position: 14px 14px;
		background-size:     16px;
	}
}

p.submit
{
	margin-top: 0;
}